import React from 'react'
import { StaticQuery, graphql, Link } from 'gatsby'
import styled from 'styled-components';

import Container from './container';
import WithLanguage from './with-language';

const Footer = styled.footer`
  background-color: #242424;
  display: block;
  overflow: visible;
  position: relative;
  text-align: center;
  width: 100%;
`;

const Navigation = styled.ul`
  margin: 0;
  padding: 15px 0;
  text-align: center;

  @media (max-width: 1200px) {
    display: none;
  }
`;

const NavigationItem = styled.nav`
  color: #fff;
  display: inline-block;
  margin: 0 50px;
`;

const Hamburger = styled.div`
  display: none;
  font-size: 1.25em;
  padding: 15px 25px 0;
  text-align: right;
  width: 100%;

  @media (max-width: 1200px) {
    display: block;
  }
`;

const HamburgerIcon = styled.span`
  color: #fff;
`;

const makeContent = (language) => (data) => {
  const content = data[language];

  return (
    <Footer>
      <Container>
        <Hamburger>
          <HamburgerIcon className="fa fa-bars" id="mobile_nav_toggle">
          </HamburgerIcon>
        </Hamburger>
        <Navigation>
          <NavigationItem>Yeet</NavigationItem>
        </Navigation>
      </Container>
    </Footer>
  );
}

const NavBar = ({ location }) => (
  <WithLanguage
    location={location}
    render={language => (
      <StaticQuery
        query={graphql`
          query {
            en: contentfulNavbar(node_locale: { eq: "en-US" }) { ...NavbarFragment }
            fr: contentfulNavbar(node_locale: { eq: "fr" }) { ...NavbarFragment }
          }
          fragment NavbarFragment on ContentfulNavbar {
            name
            pages {
              ...on ContentfulHomepage { pageName },
              ...on ContentfulAbout { pageName },
              ...on ContentfulProjects { pageName },
              ...on ContentfulPortfolio { pageName },
              ...on ContentfulConnect { pageName },
            }
            externalLinks {
              text 
              url
            }
            insertLinksAfter {
              __typename
            }
          }
        `}
        render={makeContent(language)}
      />
    )} />
)

export default NavBar
