const WithLanguage = ({ location, render }) => {
  if (location) {
    const { pathname } = location;
  
    const regex = /^\/(\w{2})\/.*$/
    const match = regex.exec(pathname);
    console.log(location);
    var languageCode = match ? match[1] : 'en-US';
  
    if (languageCode.trim() === 'en') {
      languageCode = 'en-US'
    }
  
    return render(languageCode);    
  }

  return null;
}

export default WithLanguage;